      aboutMe: 'A dedicated person who puts his heart and soul into his work. A web developer who loves to build some really cool front-end stuff using the angular framework of javascript. An open source enthusiast who loves to contribute to open source projects. A startup enthusiast trying to develop some entrepreneur spirit.',
      experience: [
        {
          position: 'Active Member',
          company: 'Entrepreneurship Cell, The LNM Institute of Information Technology, Jaipur',
          description: 'It was a very good learning experience during my tenure as an active member of the Entrepreneurship Cell. During this period, I developed some management, marketing and start-up skills.',
          startDate: 'August 2017',
          endDate: 'December 2017'
        },
        {
          position: 'Active Member, Digital Image Processing (DIP) Division',
          company: 'Phoenix Robotics Club, The LNM Institute of Information Technology, Jaipur',
          description: 'During my tenure here as an active member, I gained significant knowledge about image processing through openCV in python and some useful robotics skills.',
          startDate: 'August 2017',
          endDate: 'January 2018'
        },
        {
          position: 'Student Mentor',
          company: 'Counselling Cell, The LNM Institute of Information Technology, Jaipur',
          description: 'I worked as a student mentor here and we helped the freshmen students in their initial days of college life.',
          startDate: 'July 2017',
          endDate: 'May 2018'
        },
        {
          position: 'Member',
          company: 'Computer Society of India (CSI), The LNMIIT Student Chapter',
          description: 'During my tenure here, I developed some useful technical skills through interactive workshops and sessions.',
          startDate: 'August 2017',
          endDate: 'May 2018'
        }
      ]
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      // our custom jQuery code goes here
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      // Closes responsive menu when a scroll trigger link is clicked
      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })
      // Activate scrollspy to add active class to navbar items on scroll
      $('body').scrollspy({
        target: '#sideNav'
      })
    })
  }
}
</script>

